import { useState } from 'react';
import useWindowDimensions from '../useWindowDimensions.js';
import { Parallax,Background } from 'react-parallax';
import {useEffect} from 'react';
import Header from './header.js';

function Intro() {

  const [blur, setBlur] = useState(0);
  const [imgWidth, setImgWidth] = useState('100vw');
  const { height, width } = useWindowDimensions();
  let init = false;

  useEffect(() => {
    init = true;
  });

  return (
    <>
      <Parallax
        strength={-200}
        renderLayer={percentage => {
          setBlur(percentage - .89);
          let w = 89 + (100 * blur);
          if (((((w/100) * width)+200)/height) <=2 && init) {
            setImgWidth(w+'vw');
          }
        }}
        >

        <Background className="">
          <div className="w-screen h-screen flex flex-col items-end" style={{marginTop:'200px'}}>

          </div>
        </Background>


        <div className="m-auto w-screen flex flex-col h-screen" style={{backdropFilter: 'blur(' + 20 * (blur-.19) + 'px)'}}>
        <Header/>

          <div className="w-full flex p-5 text-center text-blue-800" style={{opacity: 1 - 2.75 * blur}}>
            <h1 className="lg:text-5xl md:text-4xl text-3xl ml-auto mr-auto">ndrwptrsn</h1>
          </div>

          <div className="w-full flex p-5 text-center text-blue-400" style={{opacity: 1 - 2.75 * blur}}>
            <h2 className="md:text-2xl text-xl ml-auto mr-auto">Web Developer, Designer, Problem Solver</h2>
          </div>

          <div className="w-full flex items-end flex-row-reverse flex-1 ">
            <div className="flex-none" style={{width: imgWidth}}>
              <img src="./portrait.svg" style={{marginBottom: '-1px'}} alt="devices"/>
            </div>
          </div>
        </div>

      </Parallax>

    </>
  );
}

export default Intro;

